        <MultiSelector
          :options_props="options_3"
          @select="option_select_multi"
          :selected_options="selected_options_3"
        />
      </div>
      <div class="row">
        <label>Название:</label
        ><input type="text" v-model="new_edit_data.title" />
      </div>
      <div class="row">
        <label>Артикул:</label
        ><input type="text" v-model="new_edit_data.description" />
      </div>
