        <label>НДС %:</label><input type="text" />
        <input type="checkbox" id="2id" class="checkbox" />
        <label for="2id">НДС включен в цену</label>
        <input type="checkbox" id="3id" class="checkbox" />
        <label for="3id">Менеджер может менять % НДС</label>
      </div>
