<template>
  <div class="wrapper">
    <transition name="mdl">
      <EditItem
        v-if="show_edit_modal"
        :edit_data="edit_data"
        @save_data="save_edit_data"
      ></EditItem
    ></transition>
    <tabel class="tabel" :key="updateKey" :class="{ blur: show_edit_modal }">
      <thead>
        <tr class="bar_row">
          <th
            class="bar_item item"
            v-show="collval[idx - 1] === false ? false : true"
            :style="{
              width:
                idx === 0
                  ? 17 + 'px' + !important
                  : idx === params.length - 1
                  ? 20 + 'px' + !important
                  : (collsCount >= 8 ? 100 : collsCount > 3 ? 90 : 80) /
                      collsCount +
                    '%',
            }"
            v-for="(param, idx) in params"
            :key="param"
          >
            <div class="bar_item_group">
              <div>{{ param }}</div>
              <button v-show="param" class="bar_item_icon"></button>
            </div>
          </th>
        </tr>
        <tr>
          <transition-group name="mdl">
            <th
              class="item"
              v-show="
                show_filter && (collval[idx - 1] === false ? false : true)
              "
              v-for="(filter, idx) in params.length"
              :key="idx"
            >
              <div class="filter">
                <button @click="changeComparison(idx)">
                  <div
                    class="icon"
                    :class="{
                      tilde: comparisons[idx] == 1,
                      equal: comparisons[idx] == 2,
                      more: comparisons[idx] == 3,
                      more_or_equal: comparisons[idx] == 4,
                      less: comparisons[idx] == 5,
                      less_or_equal: comparisons[idx] == 6,
                    }"
                  ></div>
                </button>
                <input v-model="filterValue[idx]" type="text" />
              </div></th
          ></transition-group>
        </tr>
      </thead>
      <tbody>
        <transition-group name="rows">
          <tr
            class="row"
            v-for="(row, i) in paginatedData"
            :key="row"
            v-show="!showduplicate[i + idxes[page - 1][0]]"
          >
            <td class="item">
              <input
                type="checkbox"
                class="checkbox"
                :id="i + 'a'"
                v-model="changeValue[data.indexOf(row)]"
              />
              <label :for="i + 'a'"></label>
            </td>
            <td
              :class="{ open_dublitem: duplicate[i + idxes[page - 1][0]] }"
              class="item"
              v-for="(item, idx) in Object.entries(row)"
              v-show="collval[idx] === false ? false : true"
              :key="item"
            >
              <div class="dublitem">
                {{ item[1] }}
                <button
                  v-if="
                    idx == params.indexOf('Название') - 1 &&
                    showduplicate[i + idxes[page - 1][0] + 1] &&
                    !duplicate[i + idxes[page - 1][0]]
                  "
                  @click="showduble(i + idxes[page - 1][0])"
                  class="buttonDuwn btn"
                ></button>
                <button
                  v-if="
                    idx == params.indexOf('Название') - 1 &&
                    !showduplicate[i + idxes[page - 1][0] + 1] &&
                    !duplicate[i + idxes[page - 1][0]] &&
                    duplicate[i + idxes[page - 1][0] + 1]
                  "
                  @click="hideduble(i + idxes[page - 1][0])"
                  class="buttonUp btn"
                ></button>
              </div>
            </td>
            <td class="item">
              <div class="edit_icon" @click="open_edit_modal(row, i)"></div>
            </td></tr
        ></transition-group>
      </tbody>
    </tabel>
    <div class="bottom" :class="{ blur: show_edit_modal }">
      <button v-if="page > 1" @click="page -= 1">
        {{ "<" }}
      </button>
      <span style="margin: 5px">{{ page }}</span>
      <button
        v-if="page * count < data.length - countHideRows"
        @click="page += 1"
      >
        {{ ">" }}
      </button>
      <select name="count" id="count" class="count" v-model="count">
        <option>3</option>
        <option>5</option>
        <option>10</option>
        <option>20</option>
      </select>
    </div>
  </div>
</template>

<script>
import EditItem from "@/components/EditItem.vue";
import { mapGetters } from "vuex";
export default {
  name: "Main_grid",
  components: {
    EditItem,
  },
  props: {
    params: {
      type: Array,
      required: true,
    },
    data: {
      type: Array,
      required: true,
    },
    collval: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      coll: [],
      count: 3,
      page: 1,
      filterValue: [],
      updateKey: 0,
      comparisons: [],
      changeValue: [],
      duplicate: [],
      showduplicate: [],
      startIdx: 0,
      idxes: [[0, 6]],
      edit_data: {},
    };
  },
  watch: {
    count() {
      if (this.count < 0) {
        this.count = 0;
      }
      this.startIdx = 0;
      this.feelIdxes();
    },
    paginatedData: {
      handler: function () {
        this.updateKey += 1;
        this.changeValue = [];
      },
      deep: true,
    },
    comparisons: {
      handler: function () {
        this.comparisons.forEach((val, i) => {
          val > 6 ? (this.comparisons[i] = 1) : val;
        });
      },
      deep: true,
    },
    data: {
      handler: function () {
        this.comparisons = [];
        this.fillColls();
        this.filterValue = [];
        // if (this.edit_idx != null) this.page = 1;
        this.calcDuplicate();
      },
      deep: true,
    },
    show_buttons: function () {
      this.$store.commit("open_close_buttons", this.show_buttons);
    },
    show_edit_modal: function () {
      this.$emit("show_edit_mod", this.show_edit_modal);
    },
    countHideRows: function () {
      this.feelIdxes();
    },
  },
  computed: {
    show_buttons() {
      let value = false;
      this.changeValue.forEach((val) => {
        if (val != undefined) {
          value = val || value;
        }
      });
      return value;
    },
    paginatedData() {
      const dat = this.data.slice(
        this.idxes[this.page - 1][0],
        this.idxes[this.page - 1][1]
      );
      return dat;
    },
    collsCount() {
      let count = 0;
      this.collval.forEach((val) => (val ? (count += 1) : null));
      return count;
    },
    countPage() {
      return this.count * (this.page - 1);
    },
    countHideRows() {
      let a = 0;
      this.duplicate.forEach((val) => (val ? (a += 1) : null));
      return a;
    },
    endidx() {
      return this.endId(this.startIdx);
    },
    ...mapGetters(["show_edit_modal"]),
    ...mapGetters(["show_filter"]),
  },
  mounted() {
    this.fillColls();
    this.calcDuplicate();
    this.feelIdxes();
  },
  methods: {
    fillColls() {
      this.coll = Object.keys(this.params);
      this.coll.forEach(() => {
        this.comparisons.push(1);
      });
    },
    changeComparison(idx) {
      this.comparisons[idx] = this.comparisons[idx] + 1;
    },
    calcDuplicate() {
      let name = "";
      let artic = "";
      this.duplicate = [];
      this.showduplicate = [];
      const name_idx = Object.keys(this.data[0]).indexOf("title");
      const artic_idx = Object.keys(this.data[0]).indexOf("description");
      this.data.forEach((val) => {
        const value = Object.entries(val);
        if (value[name_idx][1] == name && value[artic_idx][1] == artic) {
          this.duplicate.push(true);
        } else {
          this.duplicate.push(false);
        }
        name = value[name_idx][1];
        artic = value[artic_idx][1];
      });
      this.duplicate.forEach((val) => this.showduplicate.push(val));
    },
    showduble(idx) {
      while (this.duplicate[idx + 1]) {
        this.showduplicate[idx + 1] = false;
        idx += 1;
      }
    },
    hideduble(idx) {
      while (this.duplicate[idx + 1]) {
        this.showduplicate[idx + 1] = true;
        idx += 1;
      }
    },
    endId(idx) {
      let a = 0;
      while (a != this.count) {
        if (!this.duplicate[idx]) a += 1;
        idx += 1;
      }
      while (this.duplicate[idx]) idx += 1;
      return idx;
    },
    feelIdxes() {
      this.idxes = [];
      while (this.startIdx < this.data.length) {
        this.idxes.push([this.startIdx, this.endId(this.startIdx)]);
        this.startIdx = this.endId(this.startIdx);
      }
      this.startIdx = 0;
    },
    open_edit_modal(row, idx) {
      Object.assign(this.edit_data, row);
      this.$store.commit("open_edit_modal", idx);
    },
  },
};
</script>

